import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    MessierOrange: {
      main: '#FF7518',
      contrastText: '#fff',
    },
  },
});

export const TokenTab = observer(props => {
  const { lang, god: pageStore, base: baseStore } = useStore();
  const store = useLocalStore(() => ({
    isShowDetail: false,
    props
  }));

  return (
    <>
    { 
      <Section>
        <BorderBox css={{ padding: '0.6rem 0', cursor: 'pointer' }}
        onClick={() => {
          props.onSelectTab()
        }}>
          <FlexBox justify="between">
            <FlexBox css={{ width: 'max-content', cursor: 'pointer', '@md': { width: '32%' } }}>
              {
                <SingleSymbol imgs={['/images/home/' + props.tokenSymbol.toLowerCase() + '.png']} />
              }
              <Text size="small" color="primary" css={{ ml: '0.8rem' }}>
                <span>
                  {
                    lang.t('pool.name') + ' - ' + props.tabName
                  }
                </span>
              </Text>
            </FlexBox>
            <Text size="small" color="white" css={{ flex: 1, textAlign: 'right', cursor: 'pointer' }}>
            </Text>
            <Icon
                css={{ width: '1rem', ml: '1rem', '@md': { width: '1.375rem', ml: '1.5rem' } }}
                src={`/images/home/${props.isShowDetail ? 'dropup' : 'dropdown'}.png`}
            >
            </Icon>
          </FlexBox>
        </BorderBox>
      </Section>
    }
    </>
  );
});

