import React, { useEffect, useState } from 'react';
import { observer, useLocalStore } from 'mobx-react-lite';
import { Wrapper, Icon, FlexBox, Text, BorderBox, Button, Section, DropBox } from '../../../modules/globalStyle';
import { useStore } from '../../../store';
import { LpMining } from '../../../components/LpMining';
import { LpMiningHeader } from '../../../components/LpMiningHeader';
import { DepositViewForBSC } from '../../../components/DepositViewForBSC';
import { TokenTab } from '../../../components/TokenTab';
import { hooks } from '../../../lib/hooks';
import { generateNote, getCurrentCommitment } from '../../../lib/zSnarksWorker';
import ReactCSSTransitionGroup from "react-addons-css-transition-group";

export const Deposit = observer((props) => {
  const { lang, god, dev, base } = useStore();

  const store = useLocalStore(() => ({
    isShowDetail1: false,
    isShowDetail2: false,
  }));

  return (
    <div style={{ display: props.isChosen? 'block' : 'none' }}>
    <Wrapper>
      <LpMiningHeader />
      {/* {god.AeolusV2 && <LpMining aeolus={god.AeolusV2} />} */}
      {/* <LpMining aeolus={god.Aeolus} /> */}
      <div style={store.isShowDetail1 ? {background: '#0000006e'} : {}}>
        <TokenTab
          tokenSymbol={god.currentChain.Coin.symbol}
          tabName={god.currentChain.Coin.symbol}
          onSelectTab={() => {
            if (!god.isOnProgress)
              store.isShowDetail1 = !store.isShowDetail1;
          }}
          isShowDetail={store.isShowDetail1}
        />
      </div>
      <ReactCSSTransitionGroup
          transitionName="example"
          transitionAppear={true}
          transitionAppearTimeout={500}
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
      >
        {
          store.isShowDetail1 && (
            god.currentChain.poolList.map((pool) => {
              return !pool.XRCToken && god.isPublicInfoLoaded && god.isPrivateInfoLoaded && 
              <DepositViewForBSC
                indexOfPool={pool.id}
                chainID={god.currentChain.chainId}
                onConnect={() => { god.setShowConnecter(true) }}
                onApproveCYC={ async (token, value)=> {
                  try {
                    await god.approve({token, spender: god.CurrentSet.UniswapV2CycloneRouter.address, value, shouldMax: true})
                  } catch (error) {
                      throw new Error("Error while approving");
                  }
                }}
                onApproveXRC={ async (token, value)=> {
                  try {
                    await god.approve({token, spender: god.CurrentSet.UniswapV2CycloneRouter.address, value, shouldMax: false})
                  } catch (error) {
                      throw new Error("Error while approving");
                  }
                }}
                onDeposit={async () => {
                  const note = generateNote( god.CurrentSet.XRCToken ? god.CurrentSet.XRCToken.symbol : god.currentChain.Coin.symbol, god.CurrentSet.id, god.currentChain.chainId);
                  god.currentNote = note;
                  const res = await hooks.waitPendingPool({note: note});
                  //  alert(res);
                  if(res) {
                    try {
                      await god.deposit({ commitment: getCurrentCommitment() });
                    } catch (error) {
                      throw new Error("Error while depositing");
                    }
                  }
                  else
                  throw new Error("Error while depositing");
                }}
                isAvaliable={true}
              />
            })
          )
        } 
      </ReactCSSTransitionGroup>
      <div style={store.isShowDetail2 ? {background: '#0000006e'} : {}}>
        <TokenTab
          tokenSymbol={god.currentChain.XRCToken.symbol}
          tabName={god.currentChain.XRCToken.symbol}
          onSelectTab={() => {
            if (!god.isOnProgress)
              store.isShowDetail2 = !store.isShowDetail2;
          }}
          isShowDetail={store.isShowDetail2}
        />
      </div>
      <ReactCSSTransitionGroup
          transitionName="example"
          transitionAppear={true}
          transitionAppearTimeout={500}
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
      >
        {
          store.isShowDetail2 && (
            god.currentChain.poolList.map((pool) => {
              return pool.XRCToken && god.isPublicInfoLoaded && god.isPrivateInfoLoaded && 
              <DepositViewForBSC
                indexOfPool={pool.id}
                chainID={god.currentChain.chainId}
                onConnect={() => { god.setShowConnecter(true) }}
                onApproveCYC={ async (token, value)=> {
                  try {
                    await god.approve({token, spender: god.CurrentSet.UniswapV2CycloneRouter.address, value, shouldMax: true})
                  } catch (error) {
                      throw new Error("Error while approving");
                  }
                }}
                onApproveXRC={ async (token, value)=> {
                  try {
                    await god.approve({token, spender: god.CurrentSet.UniswapV2CycloneRouter.address, value, shouldMax: false})
                  } catch (error) {
                      throw new Error("Error while approving");
                  }
                }}
                onDeposit={async () => {
                  const note = generateNote( god.CurrentSet.XRCToken ? god.CurrentSet.XRCToken.symbol : god.currentChain.Coin.symbol, god.CurrentSet.id, god.currentChain.chainId);
                  god.currentNote = note;
                  const res = await hooks.waitPendingPool({note: note});
                  //  alert(res);
                  if(res) {
                    try {
                      await god.deposit({ commitment: getCurrentCommitment() });
                    } catch (error) {
                      throw new Error("Error while depositing");
                    }
                  }
                  else
                  throw new Error("Error while depositing");
                }}
                isAvaliable={true}
              />
            })
          )
        }
      </ReactCSSTransitionGroup>
      {/* head */}
    </Wrapper>
    </div>
  );
});