import React, { useRef, useEffect } from 'react';
import { PDFExport, savePDF } from '@progress/kendo-react-pdf';
import { observer, useLocalStore } from 'mobx-react-lite';
import { Button } from '@progress/kendo-react-buttons';
import './PDFHelper.css';


export const PDFHelper = observer((props) => {
  const pdfExportComponent = useRef(null);
  const contentArea = useRef(null);
  
  const handleExportWithComponent = (event) => {
    pdfExportComponent.current.save();
  }

  const handleExportWithFunction = (event) => {
    savePDF(contentArea.current, { paperSize: "A4" });
  }

  useEffect(() => {
    handleExportWithComponent();
  }, []);

  return (
    <div className="app-content text-center">
      <PDFExport ref={pdfExportComponent} paperSize="A4" margin="1.5cm" fileName={'messier-compliance-'+props.noteDepositAmount+'.pdf'}>
        <div ref={contentArea} className="width-100">
          <div className='header'>
            <div className='d-inlineblock'>
              <img className='header-log-icon' alt='log-icon' src={"messier_symbol.png"} />
            </div>
            <div className='d-inlineblock'>
              <p className='header-log-toptext'>MESSIER</p>
              <p className='header-log-bottomtext'>BLACKHOLE.MESSIER.APP</p>
            </div>
          </div>
          <hr />
          <div className='content'>
            <p className='content-header text-center underline'>&nbsp;COMPLIANCE REPORT &nbsp;&nbsp;</p>
            <div>
              <table>
                <tr>
                  <td colSpan={2}>
                    <p className='text-8 overflow-break1 margin-tb-0 padding-left-10'>Note: {props.note}</p>
                  </td>
                </tr>
                <tr>
                  <th className='table-key text-12 text-center'>Deposit</th>
                  <th className='table-value text-12 text-center'>Withdrawal</th>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>Amount: </label><label className='table-value'>{props.noteDepositAmount}</label></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>Amount: </label><label className='table-key'>{props.noteWithdrawAmount}</label></div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>Verified: </label><label className='table-value'>Yes</label></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>Verified: </label><label className='table-key'>Yes</label></div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>Date: </label><label className='table-value'>{props.noteTime}</label></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>Date: </label><label className='table-key'>{props.noteWithdrawTime}</label></div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>Transaction: </label><p className='table-value overflow-break margin-tb-0'><a href={props.explorerURL+'/tx/'+props.noteTx}>{props.noteTx}</a></p></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>Transaction: </label><p className='table-key overflow-break margin-tb-0'><a href={props.explorerURL+'/tx/'+props.noteWithdrawTx}>{props.noteWithdrawTx}</a></p></div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>From: </label><label className='table-value overflow-break margin-tb-0'><a href={props.explorerURL+'/address/'+props.noteFrom}>{props.noteFrom}</a></label></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>To: </label><label className='table-key overflow-break margin-tb-0'><a href={props.explorerURL+'/address/'+props.noteWithdrawTo}>{props.noteWithdrawTo}</a></label></div>
                  </td>
                </tr>
                <tr>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-key'>Commitment: </label><p className='table-value overflow-break margin-tb-0'>{props.noteCommitment}</p></div>
                  </td>
                  <td>
                    <div className='margin-l-3 text-8'><label className='table-value'>Nullifier Hash: </label><p className='table-key overflow-break margin-tb-0'>{props.noteWithdrawNulliferHash}</p></div>
                  </td>
                </tr>
              </table>
            </div>
          </div>
          <div className='footer'>
            <div className='underline text-8 text-left'>&nbsp;DISCLAIMER &nbsp;&nbsp;</div>
            <div className='text-left'>
              <p className='text-8'>THIS COMPLIANCE REPORT IS FOR INFORMATIONAL PURPOSES ONLY. YOU SHOULD CONFIRM THE VALIDITY OF THIS REPORT<br/> BY USING MESSIER S COMPLIANCE TOOL ( HTTPS : // BLACKHOLE . MESSIER.APP / COMPLIANCE ) OR VIA ANY OTHER CRYPTO<br/>GRAPHIC SOFTWARE THAT CAN COMPUTE AND VERIFY THE INFORMATION CONTAINED HEREIN ( THE "MESSIER COMPLIANCE<br/> TOOL" ).</p>
              <p className='text-8'>ANY  DISCREPANCIES  BETWEEN  INFORMATION  FOUND  IN  THIS  REPORT  AND  PROVIDED  BY  THE  ABOVE  TOOL  INDICATE  THAT<br/> THE INFORMATION IN THIS REPORT MAY BE INACCURATE AND/OR FRAUDULENT. </p>
            </div>
            <div className='text-left'>
              <div className='d-inlineblock footer-logo-text'>
                <p className='text-8'>THE COMPLIANCE REPORT IS PROVIDED "AS IS," WITHOUT WARRANTY  OF  ANY  KIND,  EXPRESS  OR<br/> IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR<br/> A PARTICULAR PURPOSE, AND NONINFRINGEMENT. IN NO EVENT SHALL THE AUTHORS OF MESSIER<br/>.APP COMPLIANCE TOOL BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, WHETHER IN<br/> AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH<br/> THIS COMPLIANCE REPORT.</p>
              </div>
              <div className='d-inlineblock text-right'>
                <img className='footer-log-icon' alt='log-icon' src={"logo-maker-for-craft-beer-brands-with-circle-badge-1205a.png"} />
              </div>
            </div>
          </div>
        </div>
      </PDFExport>
    </div>
  );
});